<template>
  <div class="family-viewer-container">
    <h1 class="mb-4">Family Recipes</h1>
    
    <div v-if="!currentRecipe">
      <p>Loading family recipes or none found...</p>
    </div>

    <div v-else>
      <div class="navigation-controls">
        <button @click="previousRecipe" :disabled="recipes.length <= 1">
          &larr; Previous
        </button>
        <span>Recipe {{ currentIndex + 1 }} of {{ recipes.length }}</span>
        <button @click="nextRecipe" :disabled="recipes.length <= 1">
          Next &rarr;
        </button>
      </div>

      <RecipeViewPage 
        :key="currentRecipe.id" 
        :recipe-prop="currentRecipe" 
      />
    </div>
  </div>
</template>

<script>
// IMPORTANT: Import the component you want to re-use
import RecipeViewPage from './RecipeViewPage.vue';

export default {
  components: {
    RecipeViewPage // Register it for use in the template
  },
  data() {
    return {
      recipes: [],
      currentIndex: 0,
    };
  },
  computed: {
    // A computed property to easily get the current recipe object
    currentRecipe() {
      return this.recipes.length > 0 ? this.recipes[this.currentIndex] : null;
    }
  },
  async created() {
    try {
      const response = await this.axios.get(this.$root.store.server_domain + "/recipes/familyrecipes");
      if (response.status === 200 && response.data.length > 0) {
        this.recipes = response.data;
      } else {
        console.warn("Failed to fetch family recipes or list is empty.");
      }
    } catch (error) {
      console.error("Error fetching family recipes:", error);
    }
  },
  methods: {
    nextRecipe() {
      this.currentIndex = (this.currentIndex + 1) % this.recipes.length;
    },
    previousRecipe() {
      this.currentIndex = (this.currentIndex - 1 + this.recipes.length) % this.recipes.length;
    }
  }
};
</script>

